.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.nav-bar {
  background-color: rgba(0, 0, 0, 0.4);
  padding: 15px 20px;
  display: flex;
  gap: 15px;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.nav-bar button {
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  border: 2px solid rgba(255, 255, 255, 0.3);
  padding: 10px 20px;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.3s ease;
  font-weight: 600;
}

.nav-bar button:hover {
  background-color: rgba(255, 255, 255, 0.3);
  border-color: rgba(255, 255, 255, 0.5);
}

.nav-bar button.active {
  background-color: #667eea;
  border-color: #667eea;
}

.App-header {
  background-color: rgba(0, 0, 0, 0.3);
  padding: 40px 20px;
  color: white;
  text-align: center;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
}

.App-header h1 {
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.App-header p {
  margin: 10px 0 0 0;
  font-size: 1.1rem;
  opacity: 0.9;
}

.content {
  flex: 1;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30px;
  padding: 40px;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
}

.card {
  background: white;
  border-radius: 10px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.3);
}

.card h2 {
  margin-top: 0;
  color: #667eea;
  font-size: 1.8rem;
}

.card p {
  color: #666;
  margin: 15px 0;
}

.card button {
  background-color: #667eea;
  color: white;
  border: none;
  padding: 10px 20px;
  margin: 5px 5px 5px 0;
  border-radius: 5px;
  font-size: 0.95rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-weight: 600;
}

.card button:hover {
  background-color: #764ba2;
}

.card button:active {
  transform: scale(0.98);
}

.card strong {
  color: #764ba2;
  font-size: 1.3rem;
}

.input-group {
  display: flex;
  gap: 10px;
  margin: 20px 0;
}

.input-group input {
  flex: 1;
  padding: 12px;
  border: 2px solid #e0e0e0;
  border-radius: 5px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.input-group input:focus {
  outline: none;
  border-color: #667eea;
}

.todo-list {
  list-style: none;
  padding: 0;
  margin: 20px 0 0 0;
}

.todo-list li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  background-color: #f9f9f9;
  border-left: 4px solid #667eea;
  margin-bottom: 10px;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

.todo-list li:hover {
  background-color: #f0f0f0;
}

.todo-list li.empty {
  justify-content: center;
  color: #999;
  border-left: none;
  background-color: #f9f9f9;
}

.todo-list li button {
  background-color: #e74c3c;
  color: white;
  padding: 8px 12px;
  font-size: 0.85rem;
  margin: 0;
}

.todo-list li button:hover {
  background-color: #c0392b;
}

.App-footer {
  background-color: rgba(0, 0, 0, 0.3);
  padding: 20px;
  color: white;
  text-align: center;
  border-top: 2px solid rgba(255, 255, 255, 0.2);
  margin-top: auto;
}

.App-footer p {
  margin: 0;
  opacity: 0.9;
}

@media (max-width: 768px) {
  .content {
    grid-template-columns: 1fr;
    padding: 20px;
    gap: 20px;
  }

  .App-header h1 {
    font-size: 1.8rem;
  }

  .card {
    padding: 20px;
  }

  .input-group {
    flex-direction: column;
  }

  .input-group input,
  .input-group button {
    width: 100%;
  }
}
